import React from 'react';
import PropTypes from 'prop-types';
import { Helmet } from 'react-helmet';

const SeoHelmet = ({ head }) => (
  <Helmet>
    <title>{head.title}</title>
    <meta name="title" content={head.title} />
    <meta name="description" content={head.description} />
    <meta property="og:locale" content={head.og.locale} />
    <meta property="og:title" content={head.og.title} />
    {head.og.image && <meta property="og:image" content={head.og.image} />}
    <meta property="og:url" content={head.og.url} />
    <meta property="og:description" content={head.og.description} />
    <meta property="og:type" content={head.og.type} />
    <meta property="og:site_name" content={head.og.site_name} />
    {head.restaurant && (
      <meta property="restaurant:restaurant" content={head.restaurant} />
    )}
    <meta name="robots" content={head.robots} />
    <link rel="canonical" href={head.canonical} />
  </Helmet>
);

SeoHelmet.propTypes = {
  head: PropTypes.object.isRequired
};

export default SeoHelmet;
