import React from 'react';
import PropTypes from 'prop-types';

const DailySpecialRight = props => {
  const style = {
    background: `url('${props.item.image}') center center / cover no-repeat`
    // background: `url('${props.item.image}')`,
    // backgroundSize: 'cover',
    // backgroundRepeat: 'no-repeat',
    // backgroundPosition: 'center center'
  };
  return (
    <div className="right-item">
      <div
        style={style}
        className={'right-item-inner'}
        onClick={e => {
          props.currentItem(props.item, props.index);
        }}
      >
        <div className="content">
          {props.item.day === '*Everyday' ? (
            <h5 className="everyday">{props.item.day}</h5>
          ) : (
            <h5>{props.item.day}</h5>
          )}
        </div>
      </div>
    </div>
  );
};

DailySpecialRight.propTypes = {
  item: PropTypes.object.isRequired
};

export default DailySpecialRight;
