import React, { Component } from 'react';
// import data from './testimonials/data';
import Testimonial from './Testimonial';
import testimonialData from '../data/TestimonialData';

class Testimonials extends Component {
  constructor(props) {
    super(props);
    this.state = {
      properties: testimonialData.properties,
      property: testimonialData.properties[0],
      intervalId: 0
    };
    // this.changeActiveSlide = this.changeActiveSlide.bind(this);
    // this.prevButton = this.prevButton.bind(this);
    // this.nextButton = this.nextButton.bind(this);
  }
  interval = 0;
  componentDidMount() {
    this.startAutoSlides();
  }
  componentWillUnmount() {
    // console.log('unmount', this.interval);
    this.pauseAutoSlides();
  }
  startAutoSlides = () => {
    this.pauseAutoSlides();
    this.interval = setInterval(this.changeActiveSlide, 20000);
  };
  pauseAutoSlides = () => {
    if (this.interval) {
      clearInterval(this.interval);
    }
  };
  handleMouseEnter = () => {
    // console.log('mouse entered');
    this.pauseAutoSlides();
  };
  handleMouseLeave = () => {
    // console.log('mouse leave');
    this.startAutoSlides();
  };

  changeActiveSlide = () => {
    this.pauseAutoSlides();
    this.startAutoSlides();
    // console.log('change', this.interval);
    const index = this.state.property.index + 1;
    if (index <= testimonialData.properties.length - 1) {
      this.setState({ property: testimonialData.properties[index] });
    } else {
      this.setState({ property: testimonialData.properties[0] });
    }
  };
  prevButton = () => {
    this.pauseAutoSlides();
    this.startAutoSlides();
    // console.log('prev', this.interval);
    if (this.state.property.index > 0) {
      const newIndex = this.state.property.index - 1;
      this.setState({ property: testimonialData.properties[newIndex] });
    }
  };

  nextButton = () => {
    this.pauseAutoSlides();
    this.startAutoSlides();
    // console.log('next', this.interval);
    if (this.state.property.index <= testimonialData.properties.length - 1) {
      const newIndex = this.state.property.index + 1;
      this.setState({ property: testimonialData.properties[newIndex] });
    }
  };

  handleTouchStart = e => {
    this.pauseAutoSlides();
  };

  handleTouchEnd = e => {
    this.startAutoSlides();
  };

  render() {
    const { properties, property } = this.state;

    return (
      <div className="testimonials">
        <div
          className="seoulContainer"
          onMouseEnter={this.handleMouseEnter}
          onMouseLeave={this.handleMouseLeave}
          onTouchStart={this.handleTouchStart}
          onTouchEnd={this.handleTouchEnd}
        >
          <h1>Reviews</h1>
          <hr className="prettyHr" />
          <div className="m-cards-slider">
            <div
              className="m-cards-slider-wrapper"
              // ref={this.testimonials}
              style={{
                transform: `translateX(-${property.index *
                  (100 / properties.length)}%)`
              }}
            >
              {properties.map(property => (
                <Testimonial key={property.id} property={property} />
              ))}
            </div>
          </div>
          <div className="button-box">
            <button onClick={this.prevButton} disabled={property.index === 0}>
              Prev
            </button>
            <button
              onClick={this.nextButton}
              disabled={
                property.index === testimonialData.properties.length - 1
              }
            >
              Next
            </button>
          </div>
        </div>
        {/* <div className="seoulContainer more-reviews">
          more reviews available from
        </div> */}
      </div>
    );
  }
}

export default Testimonials;
